<template>
    <div class="dialogWrapper" v-show="visible">
    <div class="dialog">
      <header>{{title}}
        <span class="close" @click="close"><i class="el-icon-circle-close-outline"></i></span>
      </header>
      <main>
        <slot></slot>
      </main>
    </div>
  </div>
</template>

<script>
export default {
    name: 'MyDialog',
    props: ['title', 'visible'],
    methods: {
        close() {
            this.$emit('close')
        }
    }
}
</script>

<style scoped lang="scss">
  .dialogWrapper{
    position: fixed;
    background: hsla(0, 0%,0%,0.25);
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    margin: 0;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .dialog{
    background: white;
    min-height: 4em;
    min-width: 10em;
    > header{
      padding: 16px;
      display: flex;
      justify-content: space-between;
      >span{padding:5px;}
    }
    > main{
      padding: 16px;
    }
  }
</style>


